import React, {useEffect, useState} from 'react';
import MainLayout from "../../layout/MainLayout";
import Select from 'react-select';
import axios from "axios";
import {CONFIG, PATH_NAME} from "../../utils/constants";
import ReactPlayer from "react-player";
import {Link, useHistory, useParams} from "react-router-dom";

function CameraDetail(props) {

    const [cameras, setCameras] = useState([]);
    const [selectedCamera, setSelectedCamera] = useState(null);
    const [staticVideo, setStaticVideo] = useState([
        {id: 7, stream_url: "https://hls.cradle-vision.com/stream/smartcamera9/index.m3u8"},
        {id: 9, stream_url: "https://hls.cradle-vision.com/stream/smartcamera9/index.m3u8"},
        {id: 10, stream_url: "https://hls.cradle-vision.com/stream/smartcamera9/index.m3u8"},
        {id: 11, stream_url: "https://hls.cradle-vision.com/stream/smartcamera9/index.m3u8"},
    ]);
    let params = useParams()
    let history = useHistory()

    useEffect(() => {
        getCameraList("");
    }, []);

    const getCameraList = (search) => {
        axios.get(PATH_NAME + "company/camera/smartcamera/all?page=1&per_page=100&camera_type=entry&search_str=" + search, CONFIG)
            .then(response => {
                setCameras(response.data?.items);
                console.log(response.data?.items);
            })
    };

    const selectCamera = (e) => {

        setSelectedCamera(staticVideo.filter(item => item.id == e.value)[0])
    };

    return (
        <MainLayout>
            <div className="auto-counter-page-feruz">
                <div className="add-user-page-header">
                    <Link to={"/cameras"}>
                        <img src="/assets/images/home.svg" alt=""/>
                    </Link>
                    <img className="arrow-right-img" src="/assets/images/arrow-right.svg" alt=""/>
                    <Link to={"/users"} className="users-link">
                        Камеры
                    </Link>
                    <img className="arrow-right-img" src="/assets/images/arrow-right.svg" alt=""/>
                    <span>Camera 01</span>
                </div>
                <div className="street-page-body-feruz">

                    <div className="row">

                        {/*{*/}
                        {/*    // staticVideo.filter(item => item.id == params.id)[0]?.map((item, index) => (*/}

                                <div className="col-md-12">
                                    <div className="cameras">
                                        <h4>Camera 1</h4>
                                        <ReactPlayer className="video-wrap" url={staticVideo.filter(item => item.id == params.id)[0]?.stream_url} playing={true}
                                                     muted/>

                                    </div>

                                </div>
                            {/*))*/}
                        {/*}*/}
                    </div>
                </div>
                <button type={"button"}
                        className="f-btn"
                        onClick={() => props.history.push('/cameras')}
                >
                    <img src="/assets/images/arrow-left.svg" alt=""/>
                    Назад
                </button>
            </div>
        </MainLayout>
    );
}

export default CameraDetail;
